body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  overflow: hidden; 
}
.header{
  padding: 10px; 
  background-color: firebrick;
  color: white;
  position: fixed;
  top: 0; 
  right: 0; 
  left: 0; 
  z-index: 100;
}

.header h1{
  font-size: 25px;
  padding: 0; 
  margin: 0;
  text-align: center; 
}

.map{
  width: 100vw; 
  height: 100vh; 
  position: relative; 
}

.location-icon{
  top: 80px; 
  font-size: 2rem; 
  color: red; 
}

.fire-icon{
  top: 80px; 
  font-size: 2rem; 
  color: red; 
}
.storm-icon{
  top: 80px; 
  font-size: 2rem; 
  color: rgb(252, 252, 50); 
}

.loader{
  display: flex; 
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
}

.loader img{
  width: 400px; 
}

.loader h1{
  margin-top: 0px
}

.location-info {
  position: absolute; 
  top: 50px; 
  right: 50px; 
  width: 400px; 
  min-height: 200px;
  padding: 20px; 
  background-color: rgba(0, 0, 0, 0.6);
  border-radius: 10px; 
  font-size: 18px; 
  color: white
}
.location-info ul{
  list-style-type: none;
  padding: 0;
}
.location-info li{
  padding: 5px 0; 
}